@import "styles/variables";
@import "styles/mixins";

.list {
  @include resetList;

  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 4rem 2rem;
}

@media (max-width: $adaptiveMax) {
  .list {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: $mobileMax) {
  .list {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem 1rem;
  }
}
